body {
  color: #000;
  --blue-color: #00acdc;
  --header-height: 60px;
  margin: 0;
  font-family: Roboto, system-ui;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__img {
  width: 100%;
  height: 100%;
  display: inline-block;
}

.menu {
  align-items: center;
  display: flex;
}

.menu__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.menu__item {
  margin-right: 20px;
  list-style: none;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  line-height: var(--header-height);
  white-space: nowrap;
  text-align: center;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
}

.menu__link.is-active, .menu__link:hover {
  color: var(--blue-color);
}

.menu__link.is-active {
  position: relative;
}

.menu__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
